body {
  display: block;
  height: 100%;
  width: 100%;
  margin: 0px;
  padding: 0px;
  font-family: 'Avenir Next', 'Helvetica Neue', arial, helvetica, sans-serif;
  font-size: 16px;
  line-height: 24px;
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
}
a {
  text-decoration: none;
}
a:hover {
}
a:visited {}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 15px 0px;
  font-weight: 600;
}

input::-webkit-input-placeholder, /* Chrome/Opera/Safari */
input::-moz-placeholder, /* Firefox 19+ */
input:-ms-input-placeholder, /* IE 10+ */
input:-moz-placeholder { /* Firefox 18- */
  font-size: 13px;
  color: #555;
}

.hidden {
  display: none;
}

#wrapper {
  display: block;
  position: relative;
  min-height: 100%;
  width: 100%;
}

#react-container {}

#main-window {
  display: block;
  position: absolute;
  top: 0px;
  height: 100%;
  width: auto;
  min-width: 100%;
}

.hud-window {
  z-index: 1;
  display: inline-block;
  position: fixed;
  background: rgba(255,255,255,0.6);
  border-radius: 5px;
  padding: 5px;
}

#season {
  font-size: 11px;
}

.resources > .resource {
  display: block;
}
.resource .wood {
  color: brown;
}
.resource .rock {
  background-color: grey;
}
.resource .banana {
  background-color: yellow;
}
.resource-amount, .max-amount {
  display: inline-block;
  font-size: 13px;
  vertical-align: middle;
}
.resource-amount {
  margin-left: 4px;
  font-weight: bold;
}

.max-amount {
  margin-left: 2px;
  font-size: 11px;
}

.resource-icon {
  display: inline-block;
  position: relative;
  height: 12px;
  width: 12px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  vertical-align: middle;
  background-size: contain;
}
.resource-icon.wood {
  background-image: url('images/resources/wood.png');
}
.resource-icon.rock {
  background-color: grey;
  background-image: url('images/resources/rock.png');
}
.resource-icon.banana {
  background-image: url('images/resources/banana.png');
}
.resource-icon.monkey {
  background-image: url('images/resources/monkey.png');
}

