.building-basic-banana_field {
  background-image: url('images/building/basic/banana_field.png');
}

.building-basic-banana_tree {
  background-image: url('images/building/basic/banana_tree.png');
}

.building-basic-cabin {
  background-image: url('images/building/basic/cabin.png');
}

.building-basic-crate {
  background-image: url('images/building/basic/crate.png');
}

.building-basic-mine {
  background-image: url('images/building/basic/mine.png');
}

.building-basic-sawmill {
  background-image: url('images/building/basic/sawmill.png');
}

.building-basic-wood_field {
  background-image: url('images/building/basic/wood_field.png');
}

.collect {
  height: 32px;
  width: 32px;
  margin-left: auto;
  margin-right: auto;
  display: block;
  background: white;
  border-radius: 32px;
  overflow: hidden;
}
.collect > .resource-icon {
  height: 32px;
  width: 32px;
}


